.puzzle__game__container {
    position: relative;
    display: inline-flex;
}
.puzzle__game__cover {
    z-index: 20;
    position: absolute;
    background-image: url("../view/lamborghini_square.jpg");
    background-repeat: round;
}

.puzzle__game__image__original {
}

.puzzle__game__image__unordered {
    margin: auto;
    display: flex;
    flex-basis: 100%;
    flex-grow: 0;
    flex-shrink: 0;
    flex-wrap: wrap;
    overflow: hidden;
}
